<template>
  <div class="main">
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item>
        <el-input placeholder="书名/作者/标签" v-model="formInline.search" />
      </el-form-item>
      <el-form-item>
        <el-date-picker
          v-model="formInline.time"
          type="year"
          placeholder="写作年份"
          value-format="yyyy"
          :clearable="false"
        />
      </el-form-item>
      <el-form-item>
        <el-select v-model="formInline.rate" placeholder="个人评价">
          <el-option label="10" value="10"></el-option>
          <el-option label="9" value="9"></el-option>
          <el-option label="8" value="8"></el-option>
          <el-option label="7" value="7"></el-option>
          <el-option label="6" value="6"></el-option>
          <el-option label="5" value="5"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="searchBooks">
          搜索
        </el-button>
        <el-button type="primary" icon="el-icon-refresh" @click="resetForm">
          重置
        </el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="filteredBooks"
      stripe
      :default-sort="{ prop: 'time', order: 'descending' }"
    >
      <el-table-column
        prop="name"
        label="书名"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="author"
        label="作者"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="time"
        label="写作年份"
        sortable
        align="center"
      ></el-table-column>
      <el-table-column prop="rate" label="个人评价" sortable align="center">
        <template slot-scope="scope">
          <el-rate :value="scope.row.rate / 2" disabled />
        </template>
      </el-table-column>
      <el-table-column label="标签" align="center">
        <template slot-scope="scope">
          <div v-if="scope.row.tag !== ''">
            <el-tag v-for="tag in scope.row.tag.split('/')" :key="tag">
              {{ tag }}
            </el-tag>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import books from "@/assets/books";
export default {
  data() {
    return {
      books,
      // 筛选数据
      filteredBooks: books,
      // 搜索条件
      formInline: {
        search: "",
        time: "",
        rate: "",
      },
    };
  },
  created() {
    this.$notify.info({
      title: "消息",
      message: "优书网无了，个人记录下看过的小说和评价。",
    });
    this.$notify.warning({
      title: "提示",
      message: "国产网文与日本轻小说评价标准并不完全相同",
      offset: 100,
      duration: 9000,
    });
  },
  methods: {
    // 搜索
    searchBooks() {
      let { search, time, rate } = this.formInline;
      // 无搜索条件
      if (search == "" && time == "" && rate == "") {
        return;
      }
      // 内容搜索
      if (search != "") {
        this.filteredBooks = this.filteredBooks.filter(
          (item) =>
            item.name.includes(search) ||
            item.author.includes(search) ||
            item.tag.includes(search)
        );
      }
      // 时间搜索
      if (time != "") {
        this.filteredBooks = this.filteredBooks.filter(
          (item) => item.time == time
        );
      }
      // 评价搜索
      if (rate != "") {
        this.filteredBooks = this.filteredBooks.filter(
          (item) => item.rate == rate
        );
      }
    },
    // 重置
    resetForm() {
      this.formInline.search = "";
      this.formInline.time = "";
      this.formInline.rate = "";
      this.filteredBooks = this.books;
    },
  },
};
</script>

<style lang="less" scoped>
.el-tag {
  margin: 0.3vh 0.3vw;
}
.el-form {
  margin: 2vh auto;
}
</style>